/**
 * @author 小K
 * @description 未登录状态收藏操作
 */

import { ajax } from '@common/utils'
import { authLoginUrl } from '../global/utils'

const COLLECT_HISTORY_NAME = 'collect-history'
const session = {
  get(key) {
    let obj = sessionStorage.getItem(COLLECT_HISTORY_NAME) || '{}'
    obj = JSON.parse(obj)
    return key ? obj[key] : obj
  },
  set(key, val) {
    let obj = session.get()
    obj[key] = val
    sessionStorage.setItem(COLLECT_HISTORY_NAME, JSON.stringify(obj))
  },
  del() {
    sessionStorage.removeItem(COLLECT_HISTORY_NAME)
  }
}

const { get, set, del } = session

/** 设置未登录情况记录收藏的商品id */
export const setCollectHistory = ({
  productId = '',
  isJumpLoginPage = false,
  isProDetail = false,
}) => {
  set('productId', productId)
  const offsetTopVal = document.documentElement.scrollTop || 0
  set('wrapOffsetTop', offsetTopVal)
  set('collectType', isProDetail ? 'proDetail' : 'proCard')

  if (isJumpLoginPage) {
    const { pathname, search } = location
    const targetUrl = pathname + search

    if (!targetUrl) return
    location.href = authLoginUrl(targetUrl ? targetUrl : '/home.html')
  }
}

/**
 * 回来后重新收藏并滚动到指定卡片
 */
export const recoverCollect = (prodId, cb = () => { }) => {
  if (location.pathname.indexOf('login.html') !== -1) return

  const { productId, wrapOffsetTop: scrollTop } = get()

  if (productId) {
    if (productId !== prodId) return
    ajax.post('cj/homePage/shouCangShnagPin', { productId })
      .then(([err, data]) => {
        if (err) return
        cb()
        del()

        if (scrollTop && +scrollTop > 0) {
          function scrollPosition() {
            setTimeout(() => {
              if (document.readyState === 'complete') {
                document.documentElement.scrollTop = +scrollTop
              } else {
                scrollPosition()
              }
            }, 1000)
          }

          scrollPosition()
        }
      });
  }
}

function getCollectType() {
  return get('collectType')
}

function addProDetailLoadRecoverCollect(id, cb) {
  const type = getCollectType()
  if (type !== 'proDetail') return
  window.onload = function () {
    recoverCollect(id, cb)
  }
}

function addProCardLoadRecoverCollect(id, cb) {
  // const type = getCollectType()
  const { collectType: type, productId } = get()
  if (type !== 'proCard') return
  if (id !== productId) return
  window.onload = function () {
    recoverCollect(id, cb)
  }
}

window.collectNoLogin = {
  recoverCollect,
  setCollectHistory,
  getCollectType,
  addProDetailLoadRecoverCollect,
  addProCardLoadRecoverCollect
}